import { calculateNiceNum } from './nicenum';
import { selectionToGroup } from './selectionToGroup';
import { canvasCreate, processData, fetchData } from './common';

export default function (context) {
	const fetch = require('sketch-polyfill-fetch');
	const url = context.plugin.urlForResourceNamed("params.json");
	let result = NSString.stringWithContentsOfFile_encoding_error(url, NSUTF8StringEncoding, null);
	let params = JSON.parse(result);

	let curveType = Number(params.curveType);
	const canvas = canvasCreate(context);

	function createAreaChart() {
		// Insert first zero-array
		let zeroArray = new Array();
		for (let i = 0; i < dataObj.columns; i++){
			zeroArray[i] = 0;
		};
		dataObj.table.unshift(zeroArray);
		dataObj.rows = dataObj.rows + 1;

		// Set step by X between near points
		let xStep = canvas.width / ( dataObj.columns - 1 );

		// Set first point of line
		let x0 = canvas.x,
			y0 = 0,
			y = 0,
			maxValue = dataObj.niceMax,
			minValue = 0,
			zero = canvas.y + canvas.height;

		// Check negative values
		if (dataObj.niceMax <= 0) {
			maxValue = Math.abs(dataObj.niceMin);
			zero = canvas.y;
		} else if (dataObj.niceMax >= 0 && dataObj.niceMin < 0) {
			maxValue = dataObj.niceMax - dataObj.niceMin;
			minValue = dataObj.niceMin * (-1);
		} else {
			maxValue = dataObj.niceMax;
			minValue = 0;
		}

		let mainArray = new Array();
		for (let z = 0; z < dataObj.columns; z++){
			mainArray[z] = zero - (( canvas.height / maxValue ) * (Number(dataObj.table[0][z]) + minValue) );
		}

		//Loop by number of Lines
		for (let i = 0; i < dataObj.rows - 1; i++){

			y0 = mainArray[0];

			// Create area chart
			let area = NSBezierPath.bezierPath();
			area.moveToPoint(NSMakePoint(x0,y0));

			let xLast = x0,
				yLast = y0,
				xNext = 0;

			for (let j = 1; j < dataObj.columns; j++) {

				xNext = xLast + xStep;
				y = mainArray[j];

				if (curveType === 1) {
					area.curveToPoint_controlPoint1_controlPoint2_(NSMakePoint(xNext, y), NSMakePoint(xLast + xStep/2, yLast), NSMakePoint(xNext - xStep/2, y));
				} else {
					area.lineToPoint(NSMakePoint(xNext,y));
				}

				xLast = xNext;
				yLast = y;

			};

			y = mainArray[dataObj.columns - 1] - (( canvas.height / maxValue ) * (Number(dataObj.table[i + 1][dataObj.columns - 1]) + minValue) );
			area.lineToPoint(NSMakePoint(xLast,y));
			mainArray[dataObj.columns - 1] = y;

			for (let k = dataObj.columns - 2; k >= 0; k--){
				yLast = y;
				xNext = xLast - xStep;
				y = mainArray[k] - (( canvas.height / maxValue ) * (Number(dataObj.table[i + 1][k]) + minValue) );

				if (curveType === 1) {
					area.curveToPoint_controlPoint1_controlPoint2_(NSMakePoint(xNext, y), NSMakePoint(xLast - xStep/2, yLast), NSMakePoint(xNext + xStep/2, y));
				} else {
					area.lineToPoint(NSMakePoint(xNext,y));
				}

				mainArray[k] = y;
				xLast = xNext;
			}

			area.closePath();

			// Create shape from path
			let newBezier = MSPath.pathWithBezierPath(area);
			let areaShape = MSShapeGroup.shapeWithBezierPath(newBezier),
				fill = areaShape.style().addStylePartOfType(0);
				
			fill.color = MSColor.colorWithRed_green_blue_alpha(params.colorPalete[i][0]/255,params.colorPalete[i][1]/255,params.colorPalete[i][2]/255,1);
			areaShape.setName("area_" + ( i + 1 ));

			let areaArray = NSArray.arrayWithArray([areaShape])

			// Add line and circle on artboard
			if (canvas.doc.currentPage().currentArtboard() === null){
				canvas.doc.currentPage().addLayers(areaArray);
			} else{
				canvas.doc.currentPage().currentArtboard().addLayers(areaArray);
			}
			areaShape.select_byExpandingSelection(true, true);
		}

		selectionToGroup(canvas, "Area chart");
	}

	function drawChart(){
		let rowStacked = new Array();

		for (let i = 0; i < dataObj.rows; i++) {
			for (let j = 0; j < dataObj.columns; j++) {
				rowStacked[j] = 0;
			}
		}

		for (let i = 0; i < dataObj.rows; i++) {
			for (let j = 0; j < dataObj.columns; j++) {
				rowStacked[j] += Number(dataObj.table[i][j]);
			}
		}

		dataObj.max = Math.max.apply(null, rowStacked);
		dataObj.min = Math.min.apply(null, rowStacked);

		if (dataObj.max == dataObj.min) {
			dataObj.min = 0;
		}

		let niceScales = calculateNiceNum( dataObj.min, dataObj.max );
		dataObj.niceMax = niceScales.niceMaximum;
		dataObj.niceMin = niceScales.niceMinimum;

		log(dataObj);

		if (canvas.layer.isKindOfClass(MSShapeGroup)) {
			createAreaChart();
		} else {
			let plot = canvas.layer.firstLayer();
            plot.duplicate();
            let layersInGroup = canvas.layer.containedLayersCount();
            canvas.layer.layerAtIndex(0).select_byExpandingSelection(true, true);

            for (var i = 0; i < layersInGroup - 1; i++) {
				var layer = canvas.layer.layerAtIndex(1);
				layer.removeFromParent();
			}

			canvas.layer.ungroup();

			createAreaChart();
		}
	}

	let dataObjArray = processData();
	let dataObj = 0;
	if (dataObjArray[1] === false) {
		dataObj = dataObjArray[0];
		drawChart();
	} else {
		fetchData(dataObjArray[0]).then(response => {
			dataObj = response;
			drawChart();
		})
	}
		
}
































